  // granter_cnic_img
  async granter_img() {
    const img = await Camera.getPhoto({
      quality: 90,
      resultType: CameraResultType.Base64,
      source: CameraSource.Prompt
    });
    document.getElementById('cameraImage2').setAttribute('src', `data:image/${img.format};base64,` + img.base64String);
    console.log(img.base64String);
    this.booking_details.bookingData.granter_cnic_img = img.base64String;
  }

  // user cnic img
  async user_cnic() {
    const img = await Camera.getPhoto({
      quality: 90,
      resultType: CameraResultType.Base64,
      source: CameraSource.Prompt
    });
    this.customering = `data:image/${img.format};base64,` + img.base64String ;
    document.getElementById('cameraImage').setAttribute('src', `data:image/${img.format};base64,` + img.base64String);
    console.log(img.base64String);
    this.booking_details.user.cnic_img = img.base64String;
  }
